.lz-readys .container3{-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)
}
.lz-readys .circle1{top:0;left:0
}
.lz-readys .circle2{top:0;right:0
}
.lz-readys .circle3{right:0;bottom:0
}
.lz-readys .circle4{left:0;bottom:0
}
.lz-readys .container2 .circle1{-webkit-animation-delay:-1.1s;animation-delay:-1.1s
}
.lz-readys .container3 .circle1{-webkit-animation-delay:-1s;animation-delay:-1s
