.lz-readys .container1 .circle2{-webkit-animation-delay:-0.9s;animation-delay:-0.9s
}
.lz-readys .container2 .circle2{-webkit-animation-delay:-0.8s;animation-delay:-0.8s
}
.lz-readys .container3 .circle2{-webkit-animation-delay:-0.7s;animation-delay:-0.7s
}
.lz-readys .container1 .circle3{-webkit-animation-delay:-0.6s;animation-delay:-0.6s
}
.lz-readys .container2 .circle3{-webkit-animation-delay:-0.5s;animation-delay:-0.5s
}
.lz-readys .container3 .circle3{-webkit-animation-delay:-0.4s;animation-delay:-0.4s
}
.lz-readys .container1 .circle4{-webkit-animation-delay:-0.3s;animation-delay:-0.3s
}
.lz-readys .container2 .circle4{-webkit-animation-delay:-0.2s;animation-delay:-0.2s
}
.lz-readys .container3 .circle4{-webkit-animation-delay:-0.1s;animation-delay:-0.1s
